@use "base/variables" as v;
@use "base/mixins" as m;

.gallery{
    &-images{
        @include m.grid(1);
        @include m.mobile {
            @include m.grid(2);
        }
        @include m.tablet {
            @include m.grid(3);
        }
        @include m.desktop {
            @include m.grid(4);
        }
        img{
            height: 20rem;
            object-fit: cover;
        }
    }
}

.modal{
    position: fixed;
    inset: 0;
    background-color: rgba(v.$color_black, .5);
    display: grid;
    place-items: center;
    animation: fadeIn ease-in .5s;
    &.fade-out{
        animation: fadeOut ease-in .5s;
    }
    img{
        max-width: 100rem;

    }
}

.btn-close{
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$color_black;
    color: v.$color_white;
    border: none;
    cursor: pointer;
    display: grid;
    place-items: center;
    font-weight: 700;
}

@keyframes fadeIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeOut {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}